.daily-forecast {
  background-color: #222;
}

.icon-small {
  width: 40px;
}

.daily-details-grid {
  display: grid;
  row-gap: 0;
  column-gap: 35px;
  flex: 1 1;
  grid-template-columns: auto auto;
  padding: 5px 10px;
}

.grid-item {
  display: flex;
  height: 30px;
  justify-content: space-between;
  align-items: center;
}

.daily-details-grid label:first-child {
  color: rgb(147, 146, 146);
}

.daily-details-grid label:last-child {
  color: rgb(211, 209, 209);
  /* text-align: right; */
}
