.weather-container {
  background-color: #222;
  margin: 25px auto;
}

.weather-icon {
  width: 75px;
}

.parameter-row {
  color: #fff;
  display: flex;
  justify-content: space-between;
}

.parameter-label {
  text-align: left;
  font-size: 13px;
  font-weight: 300;
  color: #d5d5d5;
}

.parameter-value {
  font-size: 12px;
  text-align: right;
  font-weight: 400;
}
